@import '../../styles/common.scss';

.logo {
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  @include theme(background-color, logoBg);
  @include theme(color, logoColor);
  border-radius: 8px;
  text-decoration: none;
}

.logo::before {
  content: 'M';
}
